	data() {
		return {
			searchKey : "",
			searchClose : false
		}
	},
	methods:{
		searchChange:function(e){
			var key = e.detail.value;
			this.searchKey = key;
			if(key.length >= 1){
				this.searchClose = true;
			}else{
				this.searchClose = false;
			}
		},
		clearKey:function(){
			this.searchClose = false;
			this.searchKey   = "";
		},
		searchNow:function(){
			uni.showToast({
				title : '开始搜索 ' + this.searchKey,
				icon  : "none"
			});
		}
	}
}
